export default {
  methods: {
    doCopy() {
      const data = this.output
      this.$copyText(data).then(
        function (e) {
          alert('複製成功')
          console.log(e)
        },
        function (e) {
          alert('Can not copy')
          console.log(e)
        },
      )
    },
  },
  computed: {
    // 輸出
    output() {
      let res = [] // `$color1: #fff\n$color2: #000`

      // global
      let {
        colorSwitch,
        borderRadius,
        colorBlack: black,
        colorGreen: green,
        colorBlue: blue,
        colorRed: red,
        colorYellow: yellow,
      } = this.configData.globalSass

      // global
      runData({ colorSwitch, borderRadius })
      // theme colos
      if (colorSwitch) {
        runData({ black, green, blue, red, yellow })
      }
      // blockGroup
      let blockGropuObj = unpack(() => {
        let {
          borderWidth: numGroupBorderWidth,
          borderRadius: numGroupBorderRadius,
          borderColor: numGroupBorderColor,
          backgroundColor: numGroupBgColor,
          color: numGroupColor,
          // afterBackgroundColor, // 膠囊 theme use
        } = this.configData.blockGroupStyle
        return {
          numGroupBorderWidth,
          numGroupBorderRadius,
          numGroupBorderColor,
          numGroupBgColor,
          numGroupColor,
        }
      })
      runData(blockGropuObj)
      // block
      let blockObj = unpack(() => {
        let {
          borderRadius: numBorderRadius,
          backgroundColor: numBgColor,
          color: numColor,
        } = this.configData.blockStyle
        return {
          numBorderRadius,
          numBgColor,
          numColor,
        }
      })
      runData(blockObj)
      // btn
      let btnObj = unpack(() => {
        let {
          borderWidth: btnBorderWidth,
          borderRadius: btnBorderRadius,
          backgroundColor: btnBgColor,
        } = this.configData.btnStyle
        let { stroke: btnColor } = this.configData.iconStyle
        return {
          btnBorderWidth,
          btnBorderRadius,
          btnBgColor,
          btnColor,
        }
      })
      runData(btnObj)

      // res = res.join(',\n')
      res = res.join(',\n  ')
      res = `@use "${this.path}" with (\n  ${res}\n);`
      return res // JSON.stringify({ title: 123 })

      function runData(dataObj) {
        Object.keys(dataObj).forEach(colorKey => {
          if (dataObj[colorKey] !== '' && dataObj[colorKey] !== null) {
            res.push(`$${colorKey}: ${dataObj[colorKey]}`)
          }
        })
      }
      function unpack(fn) {
        return fn()
      }
    },
  },
}
